<!--  -->
<template>
    <div class='content'>
        <Head title=""  :right="userInfo.roleStatus !== 5 ? '' : ''" @click-right="onClickRight" />
        <Fil :types="['park','class','intoPark']" :times="true" @change="change"/>
        <div>
			<el-table
                :data="tableData"
                border
                stripe
                style="width: 100%">
            <el-table-column
                prop="name"
                label="">
            </el-table-column>
            <el-table-column
                prop="sourceName"
                label="">
            </el-table-column>
            <el-table-column
                prop="count"
                label="">
                <template  slot-scope="scope">
                    <span>{{scope.row.count}} </span>
                </template>
            </el-table-column>
            <el-table-column
                prop="montyCount"
                label="">
                <template  slot-scope="scope">
                    <span>{{scope.row.montyCount}} </span>
                </template>
            </el-table-column>
        </el-table>
		</div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue';
import Fil from '@/components/Filter.vue';
import {mapState} from 'vuex'; 

export default {
//import
    components: {
        Head,Fil
    },
    data() {
        //
        return {
            count:'',
            techang:[],
            tableData: []
        };
    },
    //
    methods: {
        change(types){
            this.init(types)
        },
        init(types){
            console.log(types)
            let savePost = {
                parkId:types.park.id,
                classId:types.class.id,
                sourceId:types.intoPark.id,
                start:types.times[0],
                end:types.times[1],
            }
            this.axios.post('/TeacherApp/studentFiles',savePost)
            .then(res=> {
                console.log(res.data.data)
                this.tableData = res.data.data
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        onClickRight() {
			this.$router.push({
				path: '/Addthestudent'
			})
		},
    },
    // - this
    created() {
        this.init()
    },
    // - DOM
    mounted() {

    },
    computed: {
        ...mapState('user', ['userInfo'])
    },
}
</script>
<style lang='less' scoped>
//@import url(); css
table {
		width: 96%;
		margin: 0.15rem auto;
		border: solid 0.01rem rgb(221, 221, 221);
		font-family: PingFang-SC-Medium;
		font-size: 0.24rem;
		color: rgb(34, 34, 34);
	}

	td {
		width: 3.45rem;
		height: 0.8rem;
		text-align: center;
		border: solid 0.01rem rgb(221, 221, 221);
	}

	tr:nth-child(odd) {
		background-color: rgb(250, 255, 238);
	}

	tr:nth-child(even) {
		background-color: rgb(255, 255, 255);
	}
</style>