<!--  -->
<template>
    <div class='content'>
        <Head title="" right="" @click-right="onClickRight"/>
        <div class='contentQing'>
            <van-cell-group>
                <van-field
                    v-model="username"
                    label=""
                    placeholder=""
                />
            </van-cell-group>
            <van-cell-group>
                <van-cell title="">
                <template>
                <van-uploader :after-read="file=>onRead(file)" style="dicplay:inline-block" class="Tc">
                    {{file?file:''}}
                </van-uploader>
                </template>
                </van-cell>
            </van-cell-group>
        </div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
export default {
    //import
    components: {
        Head
    },
    data() {
        //
        return {
            InvData:[],
            username:'',
            file:"",
            img:""
        };
    },
    //
    methods: {
        onClickRight() {
            let savePost = {
                class: this.username,
                img: this.img,
                admin_id: this.$store.state.user.userInfo.admin_id
            }
			this.axios.post("/TeacherApp/Parentaladd",savePost).then(res=>{
                console.log(res.data.data)
            })
        },
        onRead(file){
            let data=new FormData;
            data.append("files", file.file);
            this.axios.post("/Staff/mu_add",data).then(res=>{
                this.file=file.file.name
                this.img=res.data.data
            })
        },
    },
    // - this
    created() {
        
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css
.van-cell__title{
    max-width:90px;
}
.van-cell__value{
    text-align:left;
}
</style>