<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <div>
			<el-table border :data="techang">
				<el-table-column
					v-for="(item, key) in title"
					:label="item.label"
					:prop="item.data"
					:key="key"
					:width="item.width"
				>
				</el-table-column>
			</el-table>
		</div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
import Fil from '@/components/Filter.vue'
import {WAGES_TITLE} from './common/config';
export default {
//import
    components: {
        Head,Fil
    },
    data() {
        //
        return {
            // count:'',
            techang:[]
        };
    },
    //
    methods: {
        init(){
            let savePost = {
                staff_id: this.$store.state.user.userInfo.staff_id
            }
            this.axios.post('/Wages/one_wages',savePost)
            .then(res=> {
                this.techang = res.data.data;
            })
            .catch(function (error) {
                console.log(error);
            });
        },
    },
    // - this
    created() {
		this.init()
		this.title = WAGES_TITLE;
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css
table {
		width: 96%;
		margin: 0.15rem auto;
		border: solid 0.01rem rgb(221, 221, 221);
		font-family: PingFang-SC-Medium;
		font-size: 0.24rem;
		color: rgb(34, 34, 34);
	}

	td {
		width: 3.45rem;
		height: 0.8rem;
		text-align: center;
		border: solid 0.01rem rgb(221, 221, 221);
	}

	tr:nth-child(odd) {
		background-color: rgb(250, 255, 238);
	}

	tr:nth-child(even) {
		background-color: rgb(255, 255, 255);
	}
</style>