<template>
	<van-tabbar v-model="active" active-color="#07c160" style='height:50px;'>
		<van-tabbar-item
			:icon="v.icon"
			:to="v.to"
			v-for="(v,i) in list"
			:key="i"
		>
			{{v.name}}
		</van-tabbar-item>
	</van-tabbar>
</template>

<script>
import {mapState} from 'vuex';
export default {
	data() {
		return {
			active: 1
		}
	},
	computed: {
		...mapState('user', ['userInfo'])
	},
	created(){
		const userInfo = this.userInfo;
		this.list = [
			{icon:'home-o',to:'Index',name:''},
			...(
				(userInfo.roleStatus === 2 || userInfo.roleStatus === 4 || userInfo.roleStatus === 1)
					? [
						{icon:'friends-o',to:'Resourcelist',name:''},
						{icon:'points',to:'Parkservice',name:''},
					]
					: []
			),
			...(
				(userInfo.roleStatus === 5)
					? [
						{icon:'friends-o',to:'Teachbooks',name:''},
					]
					: []
			),
			{icon:'manager-o',to:'My',name:''}
		];
		for (const key in this.list) {
				// console.log(key,this.list[key].to,this.$route.name)
			if (this.list[key].to==this.$route.name) {
				this.active=key*1
			}
		}
	}
}
</script>

