<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <div v-if='tableData'>
            <van-notice-bar style='margin-bottom:10px;' wrapable v-for='(item,index) in tableData' :key='index' :scrollable="false">
                {{item.inform_desc}}
            </van-notice-bar>
        </div>
       
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
export default {
    //import
    components: {
        Head
    },
    data() {
        //
        return {
            tableData:[]
        };
    },
    //
    methods: {
        init(){
            let savePost = {
                garden_id: this.$store.state.user.userInfo.garden_id,
                ji_id: this.$store.state.user.userInfo.jiId,
            }
            this.axios.post('/Inform/show',savePost)
            .then(res=> {
                this.tableData = res.data.data
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    // - this
    created() {
        this.init()
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
