<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <Fil :types="['park','class']" @change="change" :month="true" />
        <ul style='margin-top:5px;' v-if='list != null' >
            <li style='margin-bottom:5px;background-color:white;width:48%;display:inline-block;margin-left:1%;margin-right:1%;' v-for="(v,i) in tabledata1">
                <Echarts
                    :options="options[i]"
                    :ecStyle="{width: '100%', height: '170px'}"
                    class="fl"
                    style="width:98%;float:left;"
                ></Echarts>
                <van-row style='padding-bottom:10px;'>
                    <van-col span="12">
                        <span style='border-radius:3px;width:20px;height:10px;background-color:#000;display:inline-block;'></span> {{listMsg[i]}}%
                    </van-col>
                    <van-col span="12">
                        <span style='border-radius:3px;width:20px;height:10px;background-color:red;display:inline-block;'></span> {{listMsg[i]}}%
                    </van-col>
                </van-row>
            </li>
        </ul>
        <span style='display:block;line-height:200px;font-size:24px;margin-top:5px;width:96%;margin:2%;height:200px;background-color:white;margin-top:10px;text-align:center;' v-else>
                
        </span>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from "@/components/Header.vue"
import Fil from '@/components/Filter.vue'
import Echarts from "@/components/Echarts"
export default {
    //import
    components: {
        Head,Fil,Echarts
    },
    data() {
        //
        return {
            options:{},
            tabledata:[],
            tabledata1:[
                {key:'anquan',value:'',sex:'',radio:''},
                {key:'chengzhang',value:'',sex:'',radio:''},
                {key:'chuqin',value:'',sex:'',radio:'%'},
                {key:'guanli',value:'',sex:'',radio:''},
                {key:'gugan',value:'',sex:'',radio:''},
                {key:'jinsheng',value:'',sex:'',radio:''},
                {key:'lirun',value:'',sex:'',radio:''},
                {key:'lizhi',value:'',sex:'',radio:''},
                {key:'techeng',value:'',sex:'',radio:'%'},
                {key:'tousu',value:'',sex:'',radio:''},
                {key:'tuiyuan',value:'',sex:'',radio:''},
                {key:'zaizhi',value:'',sex:'',radio:''},
                {key:'zhaosheng',value:'',sex:'',radio:''},
            ],
            listData2:'',
            listMsg:[],
            listMag:[],
            list:[]
        };
    },
    //
    methods: {
        change(types){
            this.getList(types)
        },
        getList(types) {
            let savePost = {
                garden_id:types.park.id,
                class_id:types.class.id
            }
        this.axios
            .post("/Wages/app_tong",savePost)
            .then(res => {
                this.list = res.data.data
                for (const key in this.tabledata1) {
                    let listData = ((res.data.data[this.tabledata1[key].key]).replace(/%/g,'')).split('@')
                    this.listMsg[key] = listData[1]
                    this.listMag[key] = listData[2]
                    this.options[key].series[0].data=[{value: listData[1]},{ value: listData[2]}]
                    this.options[key].title.subtext = this.tabledata1[key].sex + listData[0] + this.tabledata1[key].radio
                    this.options[key]={...this.options[key]}
                }
                this.options={...this.options}
                this.listMsg={...this.listMsg}
                this.listMag={...this.listMag}
            });
        },
    },
    // - this
    created() {
        this.options=[]
        for (const v of this.tabledata1) {
            this.options.push({
                title: {
                    text: v.value,
                    left: 'center',
                    subtext: v.value
                },
                tooltip : {
                    trigger: 'item', 
                    formatter: "{c}%" 
                },
                series: [
                    {
                        name: "",
                        type: "pie",
                        radius: "40%",
                        data: [{ value: 30 }, { value: 100 }],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: "rgba(0, 0, 0, 0.5)"
                            }
                        },
                        itemStyle:{ 
                            normal:{ 
                                label:{ 
                                    show: true, 
                                    formatter: '{c}%' 
                                }, 
                                labelLine :{show:false} 
                            } 
                        } 
                    }
                ]
            })
        }
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
