<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <div class="title">
            <div class="title_top" v-for='(a,v) in InvData' :key='v'>
                <p class="tit">{{a.addtime}}<span>{{a.title}}</span></p>
                <ul>
                    <li><img :src="'/public/img/'+a.img"></li>
                    <li><img :src="'/public/img/'+a.img1"></li>
                    <li><img :src="'/public/img/'+a.img2"></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
export default {
//import
    components: {
        Head
    },
    data() {
        //
        return {
            InvData:[],
        };
    },
    //
    methods: {
        init(){
            let saveGet = {
                user_id: this.$store.state.user.userInfo.admin_id
            }
            this.axios.post('/TeacherApp/student',saveGet)
            .then(res=> {
                this.InvData = res.data.data
                console.log(res.data.data)
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    // - this
    created() {
        this.init()
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css

.title {
  height: auto;
  width: 100%;
  color: rgb(51, 51, 51);
  font-family: "PingFang-SC-Light";
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  margin-bottom: 1.2rem;
  box-sizing: border-box;
  background-color: white;
}
.title_top {
  height: 100%;
  width: 100%;
}
.title .title_top .tit {
  width: 100%;
  font-size: 14px;
  color: rgb(51, 51, 51);
  font-family: "PingFang-SC-Light";
  margin-bottom: 0px;
  padding-top:10px;
}

.title .title_top ul {
  margin-top: 0.3rem;
  margin-bottom: 0.3rem;
  display: flex;
  justify-content: space-between;
}

.title .title_top li {
  float: left;
  width: 32%;
  height: 2.54rem;
  list-style: none;
  /*padding-right:0.68rem;*/
}

.title .title_top li img {
  width: 100%;
  height: 100%;
}
</style>