<!--  -->
<template>
    <div class='content'>
        <Head title="" right="" @click-right="onClickRight"/>
        <div class="title">
            <div class="title_top" v-for='(a,v) in InvData' :key='v'>
                <div  v-for='(c,i) in a' :key='i'>
                    <div style='display:flex;justify-content:space-between;'>
                        <span>{{c.class}}</span>
                        <span>{{v}}</span>
                    </div>
                    
                    <ul>
                        <li><img :src="'/public/img/'+c.img"></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
export default {
    //import
    components: {
        Head
    },
    data() {
        //
        return {
            InvData:[]
        };
    },
    //
    methods: {
        init(){
            let saveGet = {
                user_id: this.$store.state.user.userInfo.admin_id
            }
            this.axios.post('/TeacherApp/Parentalshow',saveGet)
            .then(res=> {
                this.InvData = res.data.data
                console.log(res.data.data)
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        onClickRight() {
			this.$router.push({
				path: '/AddHomeproduced'
			})
		},
    },
    // - this
    created() {
         this.init()
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css

.title {
  height: auto;
  width: 100%;
  color: rgb(51, 51, 51);
  font-family: "PingFang-SC-Light";
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  margin-bottom: 1.2rem;
  box-sizing: border-box;
  background-color: white;
}
.title_top {
  height: 100%;
  width: 100%;
}
.title .title_top span {
  padding-top:10px;
  display:inline-block;
}

.title .title_top ul {
  margin-top: 0.3rem;
  margin-bottom: 0.3rem;
  display: flex;
  justify-content: space-between;
}

.title .title_top li {
  float: left;
  width: 32%;
  height: 2.54rem;
  list-style: none;
  /*padding-right:0.68rem;*/
}

.title .title_top li img {
  width: 100%;
  height: 100%;
}
</style>