<template>
    <div class='content'>
        <Head title="" />
        <Fil :types="['park']" :day="true" @change="change"/>
        <div>
			<table class="table" v-for='(item,index) in techang' :key='index'>
				<tr>
					<td></td>
					<td>{{item.chu_money}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{item.cash}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.card}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.alipay}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.deduction}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.silver}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.weixin}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.expend}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.transfer}}</td>
				</tr>
                <tr>
					<td><br />()</td>
					<td>{{item.yu_money}}</td>
				</tr>
			</table>
		</div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
import Fil from '@/components/Filter.vue'
export default {
//import
    components: {
        Head,Fil
    },
    data() {
        //
        return {
            count:'',
            techang:[],
        };
    },
    //
    methods: {
        change(types){
            this.init(types)
        },
        init(types){
            console.log(types)
            let savePost = {
                addtime:types.day,
                garden_id:types.park.id
            }
            this.axios.post('/Money/price',savePost)
            .then(res=> {
                this.techang = res.data.data
            })
            .catch(function (error) {
                console.log(error);
            });
        },
    },
    // - this
    created() {
        this.init()
    },
    // - DOM
    mounted() {

    },
}
</script>
