<!--  -->
<template>
    <div class='content'>
        <Head title="" right="" @click-left="onClickLeft" @click-right="onClickRight" />
        <van-tabs @click="onClick">
            <van-tab title="">
                <Fil :types="['park','assessmentStaff']" :month='true' @change="change"/>
                <van-collapse v-model="activeNames" v-for='(item,index) in tabledata' :key='index' accordion>
                    <van-collapse-item :title="index" :name='index'>
                        <div v-for="(a,i) in item" :key='i' style="height:24px;font-size:14px;display:flex;justify-content:space-between;">
                            <span> {{a.staff_name}} </span>
                            <span> {{a.count}} </span>
                        </div>
                    </van-collapse-item>
                </van-collapse>
            </van-tab>
            <van-tab title="">
                <Fil :types="['park','assessmentPark']" :month='true' @change="change"/>
                <van-collapse v-model="activeNames" v-for='(item,index) in tabledata1' :key='index' accordion>
                    <van-collapse-item :title="index" :name='index'>
                        <div v-for="(a,i) in item" :key='i' style="height:24px;font-size:14px;display:flex;justify-content:space-between;">
                            <span> {{a.staff_name}} </span>
                            <span> {{a.count}} </span>
                        </div>
                    </van-collapse-item>
                </van-collapse>
            </van-tab>
        </van-tabs>
        <van-popup v-model="show" style='width:70%;line-height:30px;text-indent:1em;'>
            <van-row v-for='(item,index) in msg' :key='index'>
                <van-col span="24">
                    {{item.nametype}}{{item.name}}{{item.far}}
                </van-col>
            </van-row>  
        </van-popup>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
import Fil from '@/components/Filter.vue'
export default {
//import
    components: {
        Head,Fil
    },
    data() {
        //
        return {
            msg:[],
            tabledata:[],
            tabledata1:[],
            activeNames: ['1'],
            indexList:'',
            show:false,
        };
    },
    //
    methods: {
        init(types){
            console.log(types)
            if(this.indexList  == 0){
                // 
                let saveLists = {
                    itype:2,
                    orId:types.park.id,
                    hId:types.assessmentStaff.id,
                    start:types.month
                }
                this.axios.post('/TeacherApp/Assessment',saveLists)
                .then(res=> {
                    this.tabledata = res.data.data
                })
                .catch(function (error) {
                    console.log(error);
                });
            }else{
                // 
                let saveList = {
                    itype:1,
                    orId:types.park.id,
                    hId:types.assessmentPark.id,
                    start:types.month
                }
                this.axios.post('/TeacherApp/Assessment',saveList)
                .then(res=> {
                    this.tabledata1 = res.data.data
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        },
        onClickLeft() {
            history.go(-1)
        },
        change(types){
            this.init(types)
        },
        onClickRight() {
            this.axios.post('/Scoring/stafflog')
            .then(res=> {
                this.msg = res.data.data
                this.show = true
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        onClick(index, title) {
            this.indexList = index
        },
    },
    // - this
    created() {
    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css
.footer {
    width: 100%;
    height: 6rem;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index:2003;
}
</style>