<template>
    <div class="add">
        <Head title="" right="" @click-right="addFn"/>
        <van-cell-group title="">
            <van-field label="" v-model="params.garden" :readonly="true" @click="parkF=true" placeholder="" />
            <van-picker 
                :columns="park" 
                value-key="parkName" 
                show-toolbar
                class="Fb"
                v-show="parkF" 
                @cancel="parkF=false" 
                @confirm="res=>{
                    params.garden=res.parkName;
                    params.garden_id=res.parkId;
                    parkF=false;
                    params={...params};
                }" />
            <van-field label="" v-model="params.source" :readonly="true" @click="messageF=true" placeholder="" />
            <van-picker 
                :columns="message" 
                value-key="msgName" 
                show-toolbar
                class="Fb"
                v-show="messageF" 
                @cancel="messageF=false" 
                @confirm="res=>{
                    params.source=res.msgName;
                    params.source_id=res.msgId;
                    messageF=false;
                    params={...params};
                }" />
            <van-field label="" v-model="params.intention" :readonly="true" @click="intentionF=true" placeholder="" />
            <van-picker 
                :columns="intention" 
                value-key="intName" 
                show-toolbar
                class="Fb"
                v-show="intentionF" 
                @cancel="intentionF=false" 
                @confirm="res=>{
                    params.intention=res.intName;
                    params.idea=res.intId;
                    intentionF=false;
                    params={...params};
                }" />
            <van-field label="" v-model="params.statusName" :readonly="true" @click="statusF=true" placeholder="" />
            <van-picker 
                :columns="[{id:0,name:''},{id:1,name:''}]"
                value-key="name"
                show-toolbar
                class="Fb"
                v-show="statusF"
                @cancel="statusF=false"
                @confirm="res=>{
                    params.statusName=res.name;
                    params.status=res.id;
                    statusF=false;
                    params={...params};
                }" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.tuo" placeholder="" />
            <van-field label="" v-model="params.tuo_zhou" placeholder="" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.can" placeholder="" />
            <van-field label="" v-model="params.can_zhou" placeholder="" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.xiao" placeholder="" />
            <van-field label="" v-model="params.xiao_zhou" placeholder="" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.qi" placeholder="" />
            <van-field label="" v-model="params.qi_zhou" placeholder="" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.family_name" placeholder="" />            
            <van-field label="" v-model="params.relation" placeholder="" />            
            <van-field label="" v-model="params.unit" placeholder="" />            
            <van-field label="" v-model="params.xueli" placeholder="" />            
            <van-field label="" v-model="params.tel" placeholder="" />            
            <van-field label="" v-model="params.wechat" placeholder="" />            
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.name" placeholder="" />            
            <!-- <van-field label="" v-model="params.sex" placeholder="" />  -->
            <van-field label="" v-model="params.sexName" :readonly="true" @click="sexF=true" placeholder="" />
            <van-picker 
                :columns="[{id:1,name:''},{id:0,name:''}]"
                value-key="name"
                show-toolbar
                class="Fb"
                v-show="sexF"
                @cancel="sexF=false"
                @confirm="res=>{
                    params.sexName=res.name;
                    params.sex=res.id;
                    sexF=false;
                    params={...params};
                }" />        
            <van-field label="" v-model="params.birthday" placeholder="" />            
            <van-field label="" v-model="params.place" placeholder="" />            
            <van-field label="" v-model="params.home" placeholder="" />            
            <van-field label="" v-model="params.once_garden" placeholder="" />            
        </van-cell-group>
    </div>
</template>

<script>
import Head from '@/components/Header.vue'
export default {
    components:{
        Head
    },
    data(){
        return {
            park:[],
            parkF:false,
            message:[],
            messageF:false,
            intention:[],
            intentionF:false,
            statusF:false,
            sexF:false,
            params:{
                source:"",
                source_id:""
            }
        }
    },
    methods:{
        addFn(){
            this.axios.post("/student/student_source_add", this.params)
            .then(res => {
                this.$message({
                    type: "success",
                    message: res.data.msg
                });
                this.$router.go(-1) 
            });
        }
    },
    created(){
        this.axios.post("/student/garden_where",{
            ji_id:this.$store.state.user.userInfo.jiId
        }).then(res => {
            this.message = res.data.data.messageSource;
            this.intention=res.data.data.intention;
            this.park=res.data.data.park;
        });
    }
}
</script>

<style lang="less" scoped>

</style>

