<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <Fil :types="['park','class','techang']" :times='true' @change="change"/>
        <div>

        </div>
        <van-row style='text-indent:2em;margin:10px 0px;'>
            <van-col span="12">{{count.countNum}}</van-col>
            <van-col span="12">{{count.money}}</van-col>
        </van-row>

        <van-row style='text-indent:2em;'>
            <van-col span="12">{{count.incomeMoney}}</van-col>
            <van-col span="12">{{count.expenditureMoney}}</van-col>
        </van-row>

        <div>
			<table class="table" v-for='(item,index) in techang' :key='index'>
				<tr>
					<td></td>
					<td>{{item.tName}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{item.price}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.income}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.expenditure}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.countNum}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.incomeMoney}}</td>
				</tr>
                <tr>
					<td></td>
					<td>{{item.expenditureMoney}}</td>
				</tr>
			</table>
		</div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import Head from '@/components/Header.vue'
import Fil from '@/components/Filter.vue'
export default {
//import
    components: {
        Head,Fil
    },
    data() {
        //
        return {
            count:'',
            techang:[],
        };
    },
    //
    methods: {
        change(types){
            this.init(types)
        },
        init(types){
            console.log(types)
            let saveList = {
                istype:1,
                parkId:types.park.id,
                classId:types.class.id,
                hId:types.techang.id,
                starttime:types.times[0],
                endtime:types.times[1]
            }
            this.axios.post('/Techang/counttechang',saveList)
            .then(res=> {
                this.count = res.data.count[0]
                this.techang = res.data.techang
            })
            .catch(function (error) {
                console.log(error);
            });
        },
    },
    // - this
    created() {

    },
    // - DOM
    mounted() {

    },
}
</script>
<style lang='less' scoped>
//@import url(); css
table {
		width: 96%;
		margin: 0.15rem auto;
		border: solid 0.01rem rgb(221, 221, 221);
		font-family: PingFang-SC-Medium;
		font-size: 0.24rem;
		color: rgb(34, 34, 34);
	}

	td {
		width: 3.45rem;
		height: 0.8rem;
		text-align: center;
		border: solid 0.01rem rgb(221, 221, 221);
	}

	tr:nth-child(odd) {
		background-color: rgb(250, 255, 238);
	}

	tr:nth-child(even) {
		background-color: rgb(255, 255, 255);
	}
</style>