<template>
    <div class='content'>
        <Head
		title=""
		@click-left="onClickLeft"
		/>
        <van-row>
            <van-col span="12" v-for='(item,index) in msg' :key='index'>
                <dl @click='showData=item,show=true'>
                    <dt><img :src="'/public/img/'+item.res_pic" > </dt>
                    <dd>{{item.res_name}}</dd>
                </dl>
            </van-col>
        </van-row>
        <van-dialog
        v-model="show"
        :title="showData.res_name">
            <img :src="'/public/img/'+showData.res_pic" width='100%'>
        </van-dialog>
    </div>
</template>

<script>
//jsjsjson
//import  from '';

import Head from '@/components/Header';
export default {
    //import
    components: {
		Head
	},
    data() {
        //
        return {
            active: 1,
            msg:[],
            show: false,
            showData:{},
        };
    },
    //
    methods: {
        onClickLeft() {
			history.go(-1)
		},
    },
    // - this
    created() {
        
    },
    // - DOM
    mounted() {
        this.msg = this.$route.query.msgKey
    },
}
</script>
<style lang="less" scoped>
.van-col dl{
    width:90%;
    background-color:white;
    text-align:center;
    margin:3% 5% 3% 5%;
}
.van-col dl dt{
    padding:5%;
}
.van-col dl dd{
    padding:0% 0% 5% 0%;
    font-size:14px;
}
.van-col dl dt img{
    width:100%;
    display:inline-block;
}
.van-dialog{
	img{
		margin-top: 20px;
	}
}
</style>