<!--  -->
<template>
	<div class="content">
		<Head title="" left="" @click-left="onClickLeft" />
		<div>
			<Fil :day="true" @change="confirm"/>
		</div>
		<van-collapse v-model="activeNames" v-for='(item,index) in msg' :key='index' accordion>
			<van-collapse-item :title="index" :name='index'>
				<div v-for="(a,i) in item" :key='i' style="height:24px;font-size:14px;" @click="staffName(a)">
					{{a.staff_name}}
				</div>
			</van-collapse-item>
		</van-collapse>
	</div>
</template>

<script>
	//jsjsjson
	//import  from '';
	import Head from '@/components/Header.vue'
	import Fil from '@/components/Filter.vue'
	export default {
		//import
		components: {
			Head,Fil
		},
		data() {
			//
			return {
				msg: [],
				activeNames: ['1'],
			};
		},
		//
		methods: {
			onClickLeft() {
				history.go(-1)
			},
			staffName(a) {
				this.$router.push({
					path: 'Logdetails',
					query: {
						msgKey: a
					}
				})
			},
			inputClick() {
				this.show = true
			},
			init() {
				this.axios.post('/TeacherApp/word_log')
					.then(res => {
						this.msg = res.data.data
					})
			},
			confirm(value) {
				let savePost = {
					addtime: value.day
				}
				this.axios.post('/TeacherApp/word_log', savePost)
					.then(res => {
						if (res.data.data == null) {
							this.msg = []
							this.$dialog.alert({
								message: ''
							}).then(() => {
								// on close
							});
						} else {
							this.msg = res.data.data
						}
					})
			},
			onCancel() {
				this.show = false
			},
		},
		// - this
		created() {
			this.init()
		},
		// - DOM
		mounted() {}
	};
</script>
<style lang='less' scoped>
	//@import url(); css
	.van-cell-group {
		width: 47%;
		margin: 10px 0 0 10px;
	}

	.footer {
		width: 100%;
		height: 6rem;
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: -1;
	}
</style>
