<!--  -->
<template>
    <div class='content'>
        <Head title="" />
        <ul>
            <li v-for='(item,index) in message' :key="index" @click="showFn(item)">
                <p>{{item.name}}</p>
                <span>{{item.idea1}}{{item.count1}}</span>
                <span>{{item.idea2}}{{item.count2}}</span>
                <span>{{item.idea3}}{{item.count3}}</span>
                <!-- <span class="ryxq"></span> -->
            </li>
        </ul>
    </div>
</template>

<script>
// jsjsjson
// import  from '';
import Head from '@/components/Header.vue';
export default {
    // import
    components: {
        Head,
    },
    data() {
        // 
        return {
            message: [],
        };
    },
    // 
    methods: {
        getList() {
            this.axios.post('/TeacherApp/RecruitStudents', {
                ji_id: this.$store.state.user.userInfo.jiId,
            })
                .then((res) => {
                    this.message = res.data.data;
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        showFn(o) {
            this.$router.push({
                path: '/AdmissionsList',
                query: {
                    data: o
                }
            });
        }
    },
    //  - this
    created() {
        this.getList();
    },
};
</script>
<style lang="less" scoped>
    .content {
        ul {
            padding: 10px 2%;

            li {
                width: 44%;
                margin: 3%;
                box-sizing: border-box;
                background: white;
                padding: 10px 15px;
                font-weight: bold;
                display: inline-block;

                p {
                    font-size: 16px;
                    line-height: 40px;
                }

                span {
                    line-height: 25px;
                    display: block;
                    color: #777;
                }
            }
        }
    }
</style>
