<template>
    <div class="show">
        <Head title="" right="" @click-right="addFn"/>
        <van-cell-group title="">
            <van-cell title="" :value="data.sName" />
            <van-cell title="" :value="data.source" />
            <van-cell title="" :value="data.familyName" />
            <van-cell title="" :value="data.tel" />
            <van-cell title="" :value="data.idea" />
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="params.fuzeren" placeholder="" />
            <van-field label="" v-model="params.intention" :readonly="true" @click="intentionF=true" placeholder="" />
            <van-picker 
                :columns="intention" 
                value-key="intName" 
                show-toolbar
                class="Fb"
                v-show="intentionF" 
                @cancel="intentionF=false" 
                @confirm="res=>{
                    params.intention=res.intName;
                    params.status=res.intId;
                    intentionF=false;
                    params={...params};
                }" />
            <van-field label="" v-model="params.desc" type="textarea" autosize placeholder="" />
        </van-cell-group>
    </div>
</template>

<script>
import Head from '@/components/Header.vue'
export default {
    components:{
        Head
    },
    data(){
        return {
            intention:[],
            intentionF:false,
            data:this.$route.query.o,
            params:{
                s_id:this.$route.query.o.id
            }
        }
    },
    methods:{
        addFn(){
            this.axios.post("/student/student_txadd", this.params).then(res => {
                this.$message({
                    message: res.data.msg,
                    type: "success"
                });
            });
            this.$router.go(-1) 
        }
    },
    created(){
        console.log(this.data)
        this.axios.post("/student/garden_where",{
            ji_id:this.$store.state.user.userInfo.jiId
        }).then(res => {
            this.message = res.data.data.messageSource;
            this.intention=res.data.data.intention;
            this.park=res.data.data.park;
        });
    }
}
</script>

