<!--  -->
<template>
    <div class='content'>
        <Head title="" right="" @click-right="add"/>
        <Fil :types="['park']" @change="getList"/>
        <van-cell :title="v.sName" is-link v-for="(v,i) in message" @click="showFn(v)" :key="i"/>
    </div>
</template>

<script>
// jsjsjson
// import  from '';
import Head from '@/components/Header.vue';
import Fil from '@/components/Filter.vue';
export default {
    // import
    components: {
        Head,Fil
    },
    data() {
        // 
        return {
            message: [],
        };
    },
    // 
    methods: {
        getList(data) {
            this.axios.post('/student/student_source', {
                ji_id: this.$store.state.user.userInfo.jiId,
                parkId:data.park.id
            })
            .then((res) => {
                this.message = res.data.data.student;
            })
            .catch((error) => {
                console.log(error);
            });
        },
        showFn(o) {
            this.$router.push({
                path: '/AdmissionsShow',
                query: {
                    o
                }
            });
        },
        add(){
            this.$router.push({
                path: '/AdmissionsAdd'
            });
        }
    },
    //  - this
    created() {
    },
};
</script>
<style lang="less" scoped>
    .content {
        ul {
            padding: 10px 2%;

            li {
                width: 44%;
                margin: 3%;
                box-sizing: border-box;
                background: white;
                padding: 10px 15px;
                font-weight: bold;
                display: inline-block;

                p {
                    font-size: 16px;
                    line-height: 40px;
                }

                span {
                    line-height: 25px;
                    display: block;
                    color: #777;
                }
            }
        }
    }
</style>
